.modal{
    width: 0;
    height: 0;
    position: absolute;
    z-index: 4;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 270px;
    margin: auto;
    transition: all ease .7s;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    background: #f1fff1;
    overflow: hidden;
    padding: 30px;
    box-shadow: 0px 35px 15px 9px rgba(0, 0, 0, .2);
    transform: scale(0);
    
    &.active{
        width: 1140px;
        height: 200px;
        transform: scale(1);
    }

    &-col{

        .modal-link{
            position: relative;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 39px;
            letter-spacing: -6.7332e-05px;
            color: #000000;
            display: block;
            text-decoration: none;
            margin-left: 30px;
            cursor: pointer;
            
            &:hover, &:hover::before{
                opacity: .7;
            }



            &::before{
                content: "";
                position: absolute;
                top: 0;
                left: -30px;
                bottom: 0;
                right: auto;
                margin: auto;
                width: 10px;
                height: 10px;
                background: #B4E2E1;
                border: 1px solid #4A90E2;
                border-radius: 50%;
            }
        }
    }
}
