.login-name{
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;

    .name-input{
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        box-sizing: border-box;
        border-radius: 5px;
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        color: #979797;
        padding: 20px 27px;
        width: 314px;
        margin-bottom: 26px;
        outline: none;
    }

    .name-btn{
        background: #41D481;
        border-radius: 5px;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        border: none;
        outline: none;
        padding: 20px;
    }
}