.footer{
    
    .footerTop{
        background: #282B30;
        
        .footerTopInner{
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 23px 0;

            .topLeft{
                display: flex;
                align-items: center;

                .logoFooter{
                    display: inline-block;
                    margin-right: 14px;
                }

                .logoText{
                    width: 150px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                }    
            }

            .topMiddle{
                width: 307px;
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #98A4B7;
            }

            .topRight{
                display: flex;

                .apps{
                    
                    &:first-child{
                        margin-right: 4px;
                    }
                }
            }

        }
    }

    .footerMiddle{
        background: #33383C;
        padding: 50px 0 35px 0;

        .middleInner{
            display: flex;
            align-items: flex-start;
            justify-content: space-between;

            .footerCol{

                .linkGroup{
                    margin-bottom: 35px;

                }
            }
        }

        .showMore{
           display: flex;
           align-items: center;
           justify-content: center;
            text-decoration: none;
            font-size: 15px;
            line-height: 18px;
            color: #66717A;
            
            span{
                position: relative;
                text-align: center;
                display: inline-block;

                &:hover{
                    color: #fff;
                }

                &::before{
                    content: "";
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: -130px;
                    margin: auto;
                    z-index: 2;
                    width: 12px;
                    height: 5.5px;
                    background-image: url('../../assets//images//icons/showmore.svg');
                    background-repeat: no-repeat;
                    background-position: center;
                    background-size: cover;
                }
            }
        }
    }
}

.footerBottom{
    background: #33383C;
    
    .bottomInner{
        padding: 27px 0;
        border-top: 1px solid #4C565E;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .bottomLink, .bottomLinkActive{
            font-size: 15px;
            line-height: 18px;
            color: #878F99;
            text-decoration: none;

            &:hover{
                color: #fff;
            }
        }

        .bottomLinkActive{
            color: #05F3B3;
        }
    }
}

.footerLink{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 29px;
    color: #878F99;
    display: block;
    text-decoration: none;
    transition: all ease .3s;
    
    
    &:hover{
        color: #fff;
    }
}