.showMore{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 20px 0;
    border-top: 1px solid rgba(182, 202, 251, 0.606827);

    .showMoreText{
        display: inline-block;
        font-size: 15px;
        line-height: 18px;
        color: #4E72CA;
        position: relative;
        text-decoration: none;

        &::before{
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: -140px;
            margin: auto;
            background-image: url('../../assets//images//icons/showMoreBlue.svg');
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            width: 10px;
            height: 10px;
        }
    }
}