.filter{
    padding: 30px 0;
    
    .search-wrapper{
        padding: 16px 18px;
        display: flex;
        align-items: center;
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        box-sizing: border-box;
        border-radius: 6px;
        margin-bottom: 12px;
        
        img{
            margin-right: 18px;
        }

        input{
            border: none;
            outline: none;
            width: 100%;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            
            &::placeholder{
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 19px;
                color: #304FFF;
            }
        }
    }
}

.categories{
    display: flex;
    align-items: center;
    margin-bottom: 25px;

    .categories-wrapper{
        padding: 3px 10px;
        font-style: normal;
        font-weight: bold;
        font-size: 13px;
        line-height: 16px;
        display: flex;
        align-items: center;
        border-radius: 3px;
        margin-right: 13px;

        &.blue{
            background: #E5E3FF;
            color: #704FFF;
        }

        &.cloud{
            background: #E4F6FF;
            color: #57B4E2;
        }

        &.green{
            background: #D2FFDB;
            color: #47B95E;
        }

        &.red{
            background: #F6DAC2;
            color: #874C1A;
        }

        &.city{
            background: #D8D8D8;
            color: #787993;
        }

        .categories-text{
            margin-right: 14px;
        }

        img{
            display: block;
        }
    }
}

.devices-wrapper{
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    .devices-title{
        font-size: 16px;
        line-height: 19px;
        letter-spacing: -6.7332e-05px;
        color: #787993;
        margin-right: 54px;
    }

    .devices-options-wrapper{
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.496575);
        box-sizing: border-box;
        border-radius: 27px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 7px 20px 8px 7px;
        width: 472px;
        
        .options-left{
            display: flex;
            align-items: center;

            .option{
                display: flex;
                align-items: center;
                padding-left: 20px;
                margin-right: 7px;
                background: #EBF5FF;
                border: 1px solid #B3D9FF;
                box-sizing: border-box;
                border-radius: 20.5px;
                border-right: none;

                &-text{
                    margin-right: 12px;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 18px;
                    color: #2979FF;
                    display: block;
                }

                button{
                    border: none;
                    outline: none;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: none;
                    background: #EBF5FF;
                    border: 1px solid #B3D9FF;
                    box-sizing: border-box;
                    border-radius: 20.5px;
                    height: 44px;
                    width: 44px;
                }
            }
        }
    }
}

.selections-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;

    .selections-left{

        select{
            margin-right: 60px;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            color: #414D6A;
        }
    }

    button{
        background: #304FFF;
        box-shadow: 3px 6px 14px #9CC1FF;
        border-radius: 22px;
        font-style: normal;
        font-weight: bold;
        font-size: 15px;
        line-height: 19px;
        text-align: center;
        color: #FFFFFF;
        padding: 12px 14px;
        border: none;
        outline: none;
        cursor: pointer;
    }
}