.btn-wrapper{
    display: flex;
    align-items: center;
    padding: 18px 0;
    margin-bottom: 15px;
    border-bottom: 1px solid #D5D5D5;
    
    .button{
        background: none;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        color: #9B9B9B;
        border: none;
        outline: none;
        margin-right: 50px;
        position: relative;

        &.active{
            color: #4E72CA;
        }
        
        &.active::before{
            width: 150px;
        }
        
        &::before{
            content: '';
            position: absolute;
            bottom: -20px;
            right: 0;
            left: 0;
            margin: auto;
            border-radius: 5px;
            width: 0;
            height: 3px;
            background-color: #4e72ca;
            transition: all ease .3s;
        }
        
    }
}

.container{
    width: 100%;
    max-width: 1170px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    box-sizing: border-box;
}

.searching{
    display: flex;
    align-items: center;
    padding-top: 15px;
    margin-bottom: 27px;

    img{
        margin-right: 7px;
    }

    &-title{
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 25px;
        color: #414D6A;
    }
}

.interesting{
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 25px;
    color: #41D481;
    margin-bottom: 18px;
}

.interesting-area{
    margin-bottom: 60px;
}

.login-right{
    width: 600px;
    height: 700px;
    background-image: url('../assets/images/registerImage.png');
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    position: relative;

    img{
        position: absolute;
        left: 60px;
        bottom: 120px;
        width: 250px;
        height: 250px;
        object-fit: cover;
        
    }
}

.login-main{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login-left{
    width: calc(100% - 600px);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .or{
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #787993;
        position: relative;
        margin-bottom: 26px;

        &::before, &::after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: auto;
            left: 40px;
            margin: auto;
            width:  130px;
            height: 1px;
            background-color: #979797;
        }

        &::before{
            left: auto;
            right: 40px;
        }
    }

    .form-data{
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #4E72CA;
        width: 333px;
    }

    .form-title{
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;
        color: #000000;
        margin-bottom: 50px;
    }
}