.singleItemRight{
    width: 555px;

    .top{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 17px;

        .topData{
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: -5.89155e-05px;
            color: #414D6A;
        }
    }

    .productPrice{
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 49px;
        letter-spacing: -0.00016833px;
        color: #444444;
        margin-bottom: 17px;
    }

    .address{
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -5.89154e-05px;
        text-decoration-line: underline;
        color: #4E72CA;
        cursor: pointer;
        margin-bottom: 14px;
    }

    .numbers{
        background: #65E99F;
        border-radius: 18.5px;
        width: 360px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 7px;

        .number{
            padding: 9px 0 9px 23px;
            display: inline-block;
            text-decoration: none;
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            letter-spacing: -6.73319e-05px;
            color: #000000;
        }

        .toggleBtn{
            width: 190px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #41D481;
            border-radius: 18.5px;
            border: none;
            outline: none;
            font-style: normal;
            font-weight: normal;
            font-size: 15px;
            line-height: 18px;
            color: #000000;

        }

    }

    .author{
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -3.17237e-05px;
        color: #000000;
        margin-top: 20px;
        margin-bottom: 24px;

        span{
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: -4.13787e-05px;
            text-decoration-line: underline;
            color: #2AAE63;
            margin-left: 8px;
        }
    }

    .btnWrapper{
        display: flex;
        align-items: center;
        margin-bottom: 25px;

        .btnItem{
            background: #304FFF;
            border-radius: 23.5px;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: -5.89155e-05px;
            font-weight: 500;
            color: #FFFFFF;
            padding: 15px 28px;
            border: none;
            outline: none;
            text-decoration: none;
            transform: scale(1);

            &:active{
                transform: scale(.99);
            }

            &:first-child{
                margin-right: 12px;
            }
        }
    }
}

.rightText{
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 25px;
    letter-spacing: -3.85217e-05px;
    color: #000;
    margin-bottom: 20px;
}

.textInfo{
    display: flex;
    margin-bottom: 90px;

    .infoLeft, .infoRight{
        font-style: normal;
        font-weight: 510;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -5.89155e-05px;
        color: #000000;
    }

    .infoRight{
        color: rgb(199, 18, 18);
        margin-left: 150px;
    }
}