.carouselOption{
    padding: 35px 0;
    padding-bottom: 15px;
    position: relative;
    z-index: 11;

    .slideImage{
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        outline: none;

        img{
            display: block;
            object-fit: cover;
        }
    }
}

