.articleCard{
    width: 262px;
    text-decoration: none;
    margin-bottom: 30px;
    transition: all ease .3s;
    border-radius: 5px;
    padding-bottom: 5px;

    &.active{
        background: rgba(65, 212, 129, 0.225289);
        border-radius: 6px;    
    }

    &:hover{
        box-shadow: 0 1px 1px 2px rgba(145, 119, 119, 0.2)
    }
    // height: 282px;

    .cardImage{
        width: 262px;
        height: 190px;
        object-fit: contain;
        border-radius: 5px;
        margin-bottom: 11px;
    }

    .infoWrapper{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 60px;
        padding: 0 4px;
        
        .cardTitle{
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
            margin-bottom: 6px;
        }
        
        .cardTime{
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 14px;
            color: #4A4A4A;
            margin-bottom: 6px;
        }
        
    }

        .cardPrice{
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
        padding-left: 4px;
    }
}