.header{
    padding: 16px 0;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);

    .headerTop{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 16px;

        .logoWrapper{
            display: flex;
            align-items: center;
            text-decoration: none;

            .kiwiLogo{
                width: 67px;
                height: 67px;
                margin-right: 14px;
            }

            .logoText{
                font-family: Gilroy;
                font-style: normal;
                font-weight: bold;
                font-size: 15px;
                line-height: 17px;
                color: #000000;
                width: 155px;
            }
        }

        .headerTopRight{
            display: flex;
            align-items: center;
            
            .languages{
                display: flex;
                align-items: center;
                margin-right: 66px;

                .langs{
                    text-decoration: none;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 17px;
                    color: #4A4A4A;
                    
                    &:hover{
                        opacity: .8;
                    }

                    .stick{
                        margin: 0 20px;
                    }
                }
            }

            .logIn{
                display: flex;

                .logOut{
                    font-style: normal;
                    font-weight: bold;
                    font-size: 17px;
                    line-height: 21px;
                    color: #4E72CA;
                    text-decoration: none;

                    &:hover{
                        opacity: .9;
                    }

                    .stick{
                        margin: 20px;
                    }
                }
            }
        }
    }
}
.container{
    width: 100%;
    max-width: 1170px;
    padding-left: 15px;
    padding-right: 15px;
    box-sizing: border-box;
    margin-right: auto;
    margin-left: auto;
}
