.footerWrapper{

    .footerTop{
        background: #282B30;
        
        .footerTopInner{
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 23px 0;

            .topLeft{
                display: flex;
                align-items: center;

                .logoFooter{
                    display: inline-block;
                    margin-right: 14px;
                }

                .logoText{
                    width: 150px;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                }    
            }

            .topMiddle{
                width: 307px;
                font-style: normal;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #98A4B7;
            }

            .topRight{
                display: flex;

                .apps{
                    
                    &:first-child{
                        margin-right: 4px;
                    }
                }
            }

        }
    }
}