.newItems{
    background: #FFFFFF;
    box-shadow: 12px 14px 24px rgba(0, 0, 0, 0.0647645);
    height: 0;
    overflow: hidden;

    &.active{
        height: 100%;
        overflow: auto;
    }
    
    .newItemsInner{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}