.table{
    width: 100%;
    border-left: 1px solid rgba(151, 151, 151, 0.191604);
    margin-bottom: 15px;

    &-row{
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-bottom: 1px solid rgba(151, 151, 151, 0.191604);

        &:last-child{
            border-bottom: none;
        }

        .table-col{
            padding: 20px 15px;
            width: calc(100% / 3);
            border-right: 1px solid rgba(151, 151, 151, 0.191604);

            &:last-child{
                border: none;
            }

        }
        
        .table-col-bottom{
            padding: 20px 15px;
            width: 100%;
        }
    }
}

.col-title{
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -0.00016833px;
    color: #444444;
    margin-bottom: 5px;
}

.col-data, .col-link{
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: -0.00016833px;
    color: #444444;
}

.col-link{
    color: #4E72CA;
    text-decoration-line: underline;
}